<template>
  <div>
    <div class="pb20">
      <span class="set-comp-title"></span>
    </div>
    <el-select v-model="eventParams.event" width="210px">
      <el-option v-for="item in eventOptions" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <event-item v-model="eventParams"></event-item>
  </div>

</template>

<script>
  import uploadImg from "../uploadImg/uploadImg";
  import eventItem from "../eventItem/eventItem";

  export default {
    name: "event",
    components: {
      uploadImg,
      eventItem,
    },
    data() {
      return {
        eventParams: this.value,
        eventOptions: [
          {value: '', label: ''},
          {value: 'link', label: ''},
          {value: 'page', label: ''},
          {value: 'textPop', label: ''},
          {value: 'imgPop', label: ''},
          {value: 'service', label: ''},
          {value: 'anchorPoint', label: ''},
        ],
        conditionOptions: [{value: '', label: ''}],
      }
    },
    props: {
      title: {
        require: true,
        type: String,
        default: '',
      },
      compTitle: {
        require: true,
        type: String,
        default: '',
      },
      value: {
        require: true,
        type: Object,
        default: function () {
          return {}
        },
      },
      activityId: {
        require: true,
        type: String,
        default: '',
      }
    },
    created() {
    },
    methods: {
    },
    mounted() {
    },
    watch: {
      eventParams(newVal, oldVal) {
        this.$emit('input', this.eventParams);
      },
      value(n, o) {
        this.eventParams = n;
      }
    }
  }
</script>

